@media screen and (min-width: 600px) {

.module-tuileMemories {
    width: 300px;
    height: auto;
}

.module-tuileMemories-image {
    width: 300px;
    height: 160px;
}

.module-tuileMemories-image:hover {
    width: 300px;
    height: 160px;
}

.module-tuileMemories-nouveau {
    background-color: #E85D00;
    color: white;
    font-family: var(--page-title-font-family);
    font-size: 0.9rem;
    padding: 4px 10px 2px 10px;
    width: fit-content;
}

.module-tuileMemories-titres {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 10px;
    font-family: var(--page-title-font-family);
}

.module-tuilesMemories-description {
    margin-top: 8px;
    font-family: var(--page-subtitle-font-family);
    font-size: 0.8rem;
    color: #999999;
}

.module-tuilesMemories-dateDispo {
    margin-top: 8px;
    font-family: var(--page-subtitle-font-family);
    font-size: 0.8rem;
    font-weight: bold;
    color: #999999;
}

}

